.Preview {
  position: relative;
  width: 100%;
  height: 80%;
  color: white;
  overflow: hidden;
  filter: gray(0.5);

  &.isLoading {
    .images {
      opacity: 0.2;
    }
    .spinner {
      display: block;
    }
  }

  .change-art {
    text-align: center;
    margin-bottom: 10px;
    button a {
      color: white;
    }
  }


  .spinner {
    display: none;
  }

  .images {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
  }

  img {
    top: 0;
    position: absolute;
  }
}

@media screen and (max-width: 768px) {
  .Preview {
    height: auto;

    .images {
      padding-top: 100%;
      height: auto;
    }
  }
}
