.spinner {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.spinner {
  //margin: 60px auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  font-size: 10px;
  margin: auto;
  //position: relative;
  text-indent: -9999em;
  border-top: 2.1em solid rgba(255, 255, 255, 0.2);
  border-right: 2.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 2.1em solid rgba(255, 255, 255, 0.2);
  border-left: 2.1em solid #ffffff;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
