.App {
}

.App-left-col {
  background: var(--wet-asphalt);
  flex: 5;
  max-width: 600px;
  height: 100vh;
  display: flex;
  justify-content: center;
  padding: 10px;

  .btn-wide {
    width: 100%;
  }
}

.left-col-container {
  height: 100%;
  width: 100%;
}

.App-main {
  flex: 5;
  height: 100vh;
  padding-bottom: 100px;
}

.react-tabs {
  padding: 10px 10px;
}

@media screen and (max-width: 768px) {
  .App-left-col {
    flex: 0 0 100%;
    max-width: none;
    height: auto;
    padding-bottom: 40px;

    .left-col-container {
      height: auto;
    }
  }
  .App-main {
    flex: 0 0 100%;
    height: auto;
  }
}
